import React, { useState, useEffect } from 'react';
import { useToast } from '../UI/ToastManager';
import securityService from '../../services/securityService';
import {
  ShieldCheckIcon,
  KeyIcon,
  EyeIcon,
  EyeSlashIcon,
  ExclamationTriangleIcon,
  CheckCircleIcon,
  LockClosedIcon,
  UserIcon
} from '@heroicons/react/24/outline';
import Button from '../UI/Button';
import FormField from '../UI/FormField';

const SecuritySettings = () => {
  const { showSuccess, showError, showInfo } = useToast();
  
  const [activeTab, setActiveTab] = useState('password');
  const [showPassword, setShowPassword] = useState(false);
  const [showNewPassword, setShowNewPassword] = useState(false);
  
  // tats pour le changement de mot de passe
  const [passwordData, setPasswordData] = useState({
    currentPassword: '',
    newPassword: '',
    confirmPassword: ''
  });
  
  // tats pour l'audit de scurit
  const [auditResult, setAuditResult] = useState(null);
  const [isAuditing, setIsAuditing] = useState(false);
  
  // tats pour les paramtres de scurit
  const [securitySettings, setSecuritySettings] = useState({
    sessionTimeout: 30,
    maxLoginAttempts: 5,
    requireStrongPassword: true,
    enableTwoFactor: false,
    autoLogout: true,
    encryptData: true
  });

  useEffect(() => {
    // Charger les paramtres de scurit existants
    loadSecuritySettings();
    // Effectuer un audit initial
    performInitialAudit();
  }, []);

  const loadSecuritySettings = () => {
    const saved = securityService.secureRetrieve('security_settings');
    if (saved) {
      setSecuritySettings(prev => ({ ...prev, ...saved }));
    }
  };

  const performInitialAudit = () => {
    const audit = securityService.performSecurityAudit();
    setAuditResult(audit);
  };

  // Valider la force du mot de passe en temps rel
  const passwordStrength = securityService.validatePasswordStrength(passwordData.newPassword);

  // Changer le mot de passe
  const handlePasswordChange = () => {
    if (!passwordData.currentPassword) {
      showError('Veuillez saisir votre mot de passe actuel');
      return;
    }

    if (!passwordData.newPassword) {
      showError('Veuillez saisir un nouveau mot de passe');
      return;
    }

    if (passwordData.newPassword !== passwordData.confirmPassword) {
      showError('Les mots de passe ne correspondent pas');
      return;
    }

    if (!passwordStrength.isValid) {
      showError('Le mot de passe ne respecte pas les critres de scurit');
      return;
    }

    // Simuler la vrification du mot de passe actuel
    // En production, ceci serait vrifi ct serveur
    const currentPasswordHash = securityService.secureRetrieve('user_password');
    
    try {
      // Hasher le nouveau mot de passe
      const hashedPassword = securityService.hashPassword(passwordData.newPassword);
      
      // Sauvegarder le nouveau mot de passe
      securityService.secureStore('user_password', hashedPassword);
      
      // Enregistrer l'vnement de scurit
      securityService.recordLoginAttempt('admin', true);
      
      showSuccess('Mot de passe modifi avec succs');
      
      // Rinitialiser le formulaire
      setPasswordData({
        currentPassword: '',
        newPassword: '',
        confirmPassword: ''
      });
    } catch (error) {
      showError('Erreur lors du changement de mot de passe');
    }
  };

  // Effectuer un audit de scurit
  const handleSecurityAudit = async () => {
    setIsAuditing(true);
    try {
      // Simuler un dlai d'audit
      await new Promise(resolve => setTimeout(resolve, 2000));
      
      const audit = securityService.performSecurityAudit();
      setAuditResult(audit);
      
      showSuccess('Audit de scurit termin');
    } catch (error) {
      showError('Erreur lors de l\'audit de scurit');
    } finally {
      setIsAuditing(false);
    }
  };

  // Sauvegarder les paramtres de scurit
  const handleSaveSecuritySettings = () => {
    try {
      securityService.secureStore('security_settings', securitySettings);
      showSuccess('Paramtres de scurit sauvegards');
    } catch (error) {
      showError('Erreur lors de la sauvegarde');
    }
  };

  // Nettoyer les donnes de scurit
  const handleSecurityCleanup = () => {
    try {
      securityService.secureCleanup();
      showSuccess('Nettoyage de scurit effectu');
      performInitialAudit();
    } catch (error) {
      showError('Erreur lors du nettoyage');
    }
  };

  // Gnrer un code de vrification
  const handleGenerateVerificationCode = () => {
    const code = securityService.generateVerificationCode();
    showInfo(`Code de vrification gnr: ${code}`);
  };

  const tabs = [
    { id: 'password', label: 'Mot de passe', icon: KeyIcon },
    { id: 'settings', label: 'Paramtres', icon: LockClosedIcon },
    { id: 'audit', label: 'Audit', icon: ShieldCheckIcon }
  ];

  const getStrengthColor = (strength) => {
    switch (strength) {
      case 'fort': return 'text-emerald-400';
      case 'moyen': return 'text-yellow-400';
      case 'faible': return 'text-red-400';
      default: return 'text-gray-400';
    }
  };

  const getStrengthWidth = (score) => {
    return `${(score / 5) * 100}%`;
  };

  return (
    <div className="space-y-6">
      {/* En-tte */}
      <div className="flex items-center space-x-3">
        <div className="p-2 bg-emerald-500/10 rounded-lg">
          <ShieldCheckIcon className="h-6 w-6 text-emerald-400" />
        </div>
        <div>
          <h2 className="text-xl font-semibold text-white">Scurit</h2>
          <p className="text-gray-400">Protgez votre application et vos donnes</p>
        </div>
      </div>

      {/* Alerte de scurit si ncessaire */}
      {auditResult && auditResult.recommendations.length > 0 && (
        <div className="bg-yellow-500/10 border border-yellow-500/20 rounded-lg p-4">
          <div className="flex items-start space-x-2">
            <ExclamationTriangleIcon className="h-5 w-5 text-yellow-400 mt-0.5" />
            <div>
              <p className="text-yellow-400 font-medium">Recommandations de scurit</p>
              <ul className="text-yellow-300 text-sm mt-1 space-y-1">
                {auditResult.recommendations.map((rec, index) => (
                  <li key={index}> {rec}</li>
                ))}
              </ul>
            </div>
          </div>
        </div>
      )}

      {/* Onglets */}
      <div className="border-b border-gray-700">
        <nav className="flex space-x-8">
          {tabs.map((tab) => (
            <button
              key={tab.id}
              onClick={() => setActiveTab(tab.id)}
              className={`flex items-center space-x-2 py-2 px-1 border-b-2 font-medium text-sm transition-colors ${
                activeTab === tab.id
                  ? 'border-emerald-500 text-emerald-400'
                  : 'border-transparent text-gray-400 hover:text-gray-300'
              }`}
            >
              <tab.icon className="h-4 w-4" />
              <span>{tab.label}</span>
            </button>
          ))}
        </nav>
      </div>

      {/* Contenu des onglets */}
      {activeTab === 'password' && (
        <div className="space-y-6">
          {/* Changement de mot de passe */}
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <h3 className="text-lg font-semibold text-white mb-4 flex items-center">
              <KeyIcon className="h-5 w-5 text-emerald-400 mr-2" />
              Changer le mot de passe
            </h3>
            
            <div className="space-y-4">
              <div className="relative">
                <FormField
                  label="Mot de passe actuel"
                  type={showPassword ? 'text' : 'password'}
                  value={passwordData.currentPassword}
                  onChange={(e) => setPasswordData(prev => ({ ...prev, currentPassword: e.target.value }))}
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowPassword(!showPassword)}
                  className="absolute right-3 top-9 text-gray-400 hover:text-gray-300"
                >
                  {showPassword ? <EyeSlashIcon className="h-5 w-5" /> : <EyeIcon className="h-5 w-5" />}
                </button>
              </div>
              
              <div className="relative">
                <FormField
                  label="Nouveau mot de passe"
                  type={showNewPassword ? 'text' : 'password'}
                  value={passwordData.newPassword}
                  onChange={(e) => setPasswordData(prev => ({ ...prev, newPassword: e.target.value }))}
                  required
                />
                <button
                  type="button"
                  onClick={() => setShowNewPassword(!showNewPassword)}
                  className="absolute right-3 top-9 text-gray-400 hover:text-gray-300"
                >
                  {showNewPassword ? <EyeSlashIcon className="h-5 w-5" /> : <EyeIcon className="h-5 w-5" />}
                </button>
              </div>
              
              {/* Indicateur de force du mot de passe */}
              {passwordData.newPassword && (
                <div className="space-y-2">
                  <div className="flex justify-between items-center">
                    <span className="text-sm text-gray-400">Force du mot de passe:</span>
                    <span className={`text-sm font-medium ${getStrengthColor(passwordStrength.strength)}`}>
                      {passwordStrength.strength.charAt(0).toUpperCase() + passwordStrength.strength.slice(1)}
                    </span>
                  </div>
                  <div className="w-full bg-gray-700 rounded-full h-2">
                    <div 
                      className={`h-2 rounded-full transition-all duration-300 ${
                        passwordStrength.strength === 'fort' ? 'bg-emerald-500' :
                        passwordStrength.strength === 'moyen' ? 'bg-yellow-500' : 'bg-red-500'
                      }`}
                      style={{ width: getStrengthWidth(passwordStrength.score) }}
                    ></div>
                  </div>
                  
                  {/* Critres de mot de passe */}
                  <div className="grid grid-cols-2 gap-2 text-xs">
                    {Object.entries(passwordStrength.requirements).map(([key, met]) => (
                      <div key={key} className={`flex items-center space-x-1 ${met ? 'text-emerald-400' : 'text-gray-400'}`}>
                        <CheckCircleIcon className="h-3 w-3" />
                        <span>
                          {key === 'minLength' && '8+ caractres'}
                          {key === 'hasUpperCase' && 'Majuscule'}
                          {key === 'hasLowerCase' && 'Minuscule'}
                          {key === 'hasNumbers' && 'Chiffre'}
                          {key === 'hasSpecialChar' && 'Caractre spcial'}
                        </span>
                      </div>
                    ))}
                  </div>
                </div>
              )}
              
              <FormField
                label="Confirmer le nouveau mot de passe"
                type="password"
                value={passwordData.confirmPassword}
                onChange={(e) => setPasswordData(prev => ({ ...prev, confirmPassword: e.target.value }))}
                required
              />
              
              <Button
                onClick={handlePasswordChange}
                variant="primary"
                disabled={!passwordStrength.isValid || passwordData.newPassword !== passwordData.confirmPassword}
              >
                Changer le mot de passe
              </Button>
            </div>
          </div>
        </div>
      )}

      {activeTab === 'settings' && (
        <div className="space-y-6">
          {/* Paramtres de session */}
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <h3 className="text-lg font-semibold text-white mb-4">Paramtres de session</h3>
            
            <div className="space-y-4">
              <FormField
                label="Dlai d'expiration de session (minutes)"
                type="number"
                value={securitySettings.sessionTimeout}
                onChange={(e) => setSecuritySettings(prev => ({ ...prev, sessionTimeout: parseInt(e.target.value) }))}
                min="5"
                max="120"
              />
              
              <FormField
                label="Tentatives de connexion maximales"
                type="number"
                value={securitySettings.maxLoginAttempts}
                onChange={(e) => setSecuritySettings(prev => ({ ...prev, maxLoginAttempts: parseInt(e.target.value) }))}
                min="3"
                max="10"
              />
            </div>
          </div>

          {/* Options de scurit */}
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <h3 className="text-lg font-semibold text-white mb-4">Options de scurit</h3>
            
            <div className="space-y-4">
              <label className="flex items-center justify-between">
                <div>
                  <span className="text-white font-medium">Mot de passe fort requis</span>
                  <p className="text-sm text-gray-400">Exiger des mots de passe complexes</p>
                </div>
                <input
                  type="checkbox"
                  checked={securitySettings.requireStrongPassword}
                  onChange={(e) => setSecuritySettings(prev => ({ ...prev, requireStrongPassword: e.target.checked }))}
                  className="rounded border-gray-600 text-emerald-600 focus:ring-emerald-500"
                />
              </label>
              
              <label className="flex items-center justify-between">
                <div>
                  <span className="text-white font-medium">Dconnexion automatique</span>
                  <p className="text-sm text-gray-400">Se dconnecter automatiquement aprs inactivit</p>
                </div>
                <input
                  type="checkbox"
                  checked={securitySettings.autoLogout}
                  onChange={(e) => setSecuritySettings(prev => ({ ...prev, autoLogout: e.target.checked }))}
                  className="rounded border-gray-600 text-emerald-600 focus:ring-emerald-500"
                />
              </label>
              
              <label className="flex items-center justify-between">
                <div>
                  <span className="text-white font-medium">Chiffrement des donnes</span>
                  <p className="text-sm text-gray-400">Chiffrer les donnes sensibles</p>
                </div>
                <input
                  type="checkbox"
                  checked={securitySettings.encryptData}
                  onChange={(e) => setSecuritySettings(prev => ({ ...prev, encryptData: e.target.checked }))}
                  className="rounded border-gray-600 text-emerald-600 focus:ring-emerald-500"
                />
              </label>
              
              <label className="flex items-center justify-between">
                <div>
                  <span className="text-white font-medium">Authentification  deux facteurs</span>
                  <p className="text-sm text-gray-400">Scurit supplmentaire avec code SMS</p>
                </div>
                <input
                  type="checkbox"
                  checked={securitySettings.enableTwoFactor}
                  onChange={(e) => setSecuritySettings(prev => ({ ...prev, enableTwoFactor: e.target.checked }))}
                  className="rounded border-gray-600 text-emerald-600 focus:ring-emerald-500"
                />
              </label>
            </div>
          </div>

          {/* Actions de scurit */}
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <h3 className="text-lg font-semibold text-white mb-4">Actions de scurit</h3>
            
            <div className="flex flex-wrap gap-3">
              <Button
                onClick={handleSaveSecuritySettings}
                variant="primary"
              >
                Sauvegarder les paramtres
              </Button>
              
              <Button
                onClick={handleGenerateVerificationCode}
                variant="secondary"
              >
                Gnrer code de vrification
              </Button>
              
              <Button
                onClick={handleSecurityCleanup}
                variant="outline"
              >
                Nettoyer les donnes de scurit
              </Button>
            </div>
          </div>
        </div>
      )}

      {activeTab === 'audit' && (
        <div className="space-y-6">
          {/* Audit de scurit */}
          <div className="bg-gray-800 rounded-lg p-6 border border-gray-700">
            <div className="flex justify-between items-center mb-4">
              <h3 className="text-lg font-semibold text-white">Audit de scurit</h3>
              <Button
                onClick={handleSecurityAudit}
                loading={isAuditing}
                variant="primary"
                icon={ShieldCheckIcon}
              >
                {isAuditing ? 'Audit en cours...' : 'Lancer un audit'}
              </Button>
            </div>
            
            {auditResult && (
              <div className="space-y-4">
                <div className="text-sm text-gray-400">
                  Dernier audit: {new Date(auditResult.timestamp).toLocaleString('fr-FR')}
                </div>
                
                {/* Rsultats des vrifications */}
                <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                  {Object.entries(auditResult.checks).map(([check, passed]) => (
                    <div key={check} className={`flex items-center space-x-2 p-3 rounded-lg ${
                      passed ? 'bg-emerald-500/10 border border-emerald-500/20' : 'bg-red-500/10 border border-red-500/20'
                    }`}>
                      {passed ? (
                        <CheckCircleIcon className="h-5 w-5 text-emerald-400" />
                      ) : (
                        <ExclamationTriangleIcon className="h-5 w-5 text-red-400" />
                      )}
                      <span className={passed ? 'text-emerald-400' : 'text-red-400'}>
                        {check === 'sessionValid' && 'Session valide'}
                        {check === 'passwordPolicy' && 'Politique de mot de passe'}
                        {check === 'dataEncryption' && 'Chiffrement des donnes'}
                        {check === 'backupSecurity' && 'Scurit des sauvegardes'}
                      </span>
                    </div>
                  ))}
                </div>
              </div>
            )}
          </div>
        </div>
      )}
    </div>
  );
};

export default SecuritySettings;
